.button-container {
    display: flex; /* Use flexbox for centering */
    justify-content: center; /* Horizontally center content */
}

.my-button {
    display: inline-block;
    padding: 10px 20px;
    background-color: #384CFB; /* Blue color for the button */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: 500;
    border-color: white;
    border-width: 5px;
  }
